<template><div class="markdown-body"><h1>列表</h1>
<p><code v-pre="">mu-list</code> 列表作为一个单一的连续元素来以垂直排列的方式显示多行条目。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p><code v-pre="">mu-list</code> 需和 <code v-pre="">mu-list-item</code> 组件配合使用，通过不同的组件来组成列表项的各个部分。下面就是一个只有图标和标题的示例：</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-paper :z-depth=\&quot;1\&quot; class=\&quot;demo-list-wrap\&quot;&gt;\n  &lt;mu-appbar color=\&quot;teal\&quot;&gt;\n    &lt;mu-button icon slot=\&quot;left\&quot;&gt;\n      &lt;mu-icon value=\&quot;menu\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;/mu-button&gt;\n    Menus\n  &lt;/mu-appbar&gt;\n  &lt;mu-list&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;inbox\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Inbox&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;grade\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Stared&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;send\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Sent mail&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;drafts\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Drafts&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n  &lt;/mu-list&gt;\n  &lt;mu-divider&gt;&lt;/mu-divider&gt;\n  &lt;mu-list&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-title&gt;All mail&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;info\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-title&gt;Trash&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;info\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-title&gt;Spam&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;info\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-title&gt;Follow up&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;info\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n  &lt;/mu-list&gt;\n&lt;/mu-paper&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:&quot;\n.demo-list-wrap {\n  width: 100%;\n  max-width: 360px;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-paper :z-depth="1" class="demo-list-wrap">
  <mu-appbar color="teal">
    <mu-button icon="" slot="left">
      <mu-icon value="menu"></mu-icon>
    </mu-button>
    Menus
  </mu-appbar>
  <mu-list>
    <mu-list-item button="" :ripple="false">
      <mu-list-item-action>
        <mu-icon value="inbox"></mu-icon>
      </mu-list-item-action>
      <mu-list-item-title>Inbox</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="" :ripple="false">
      <mu-list-item-action>
        <mu-icon value="grade"></mu-icon>
      </mu-list-item-action>
      <mu-list-item-title>Stared</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="" :ripple="false">
      <mu-list-item-action>
        <mu-icon value="send"></mu-icon>
      </mu-list-item-action>
      <mu-list-item-title>Sent mail</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="" :ripple="false">
      <mu-list-item-action>
        <mu-icon value="drafts"></mu-icon>
      </mu-list-item-action>
      <mu-list-item-title>Drafts</mu-list-item-title>
    </mu-list-item>
  </mu-list>
  <mu-divider></mu-divider>
  <mu-list>
    <mu-list-item button="" :ripple="false">
      <mu-list-item-title>All mail</mu-list-item-title>
      <mu-list-item-action>
        <mu-icon value="info"></mu-icon>
      </mu-list-item-action>
    </mu-list-item>
    <mu-list-item button="" :ripple="false">
      <mu-list-item-title>Trash</mu-list-item-title>
      <mu-list-item-action>
        <mu-icon value="info"></mu-icon>
      </mu-list-item-action>
    </mu-list-item>
    <mu-list-item button="" :ripple="false">
      <mu-list-item-title>Spam</mu-list-item-title>
      <mu-list-item-action>
        <mu-icon value="info"></mu-icon>
      </mu-list-item-action>
    </mu-list-item>
    <mu-list-item button="" :ripple="false">
      <mu-list-item-title>Follow up</mu-list-item-title>
      <mu-list-item-action>
        <mu-icon value="info"></mu-icon>
      </mu-list-item-action>
    </mu-list-item>
  </mu-list>
</mu-paper>

</div>
                    
