<h2 id="avatar-he-icon-de-lie-biao"><a class="header-anchor" href="#avatar-he-icon-de-lie-biao" aria-hidden="true">¶</a> Avatar 和 Icon 的列表</h2>
<p>当 <code v-pre="">mu-list-item</code> 内容中包含 <code v-pre="">mu-avatar</code> 组件时需要在 <code v-pre="">mu-list-item</code> 和 <code v-pre="">mu-list-item-action</code> 组件中设定 <code v-pre="">avatar</code> 参数。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-paper :z-depth=\&quot;1\&quot; class=\&quot;demo-list-wrap\&quot;&gt;\n  &lt;mu-appbar color=\&quot;lightBlue400\&quot;&gt;\n    &lt;mu-button icon slot=\&quot;left\&quot;&gt;\n      &lt;mu-icon value=\&quot;menu\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;/mu-button&gt;\n    Contacts\n    &lt;mu-button icon slot=\&quot;right\&quot;&gt;\n      &lt;mu-icon value=\&quot;search\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;/mu-button&gt;\n  &lt;/mu-appbar&gt;\n  &lt;mu-list&gt;\n    &lt;mu-sub-header&gt;Today&lt;/mu-sub-header&gt;\n    &lt;mu-list-item avatar=\&quot;\&quot; button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-avatar&gt;\n          &lt;img src=\&quot;../../assets/images/avatar1.jpg\&quot;&gt;\n        &lt;/mu-avatar&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Mike Li&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;chat_bubble\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item avatar=\&quot;\&quot; button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-avatar&gt;\n          &lt;img src=\&quot;../../assets/images/avatar2.jpg\&quot;&gt;\n        &lt;/mu-avatar&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Maco Mai&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;chat_bubble\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item avatar=\&quot;\&quot; button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-avatar&gt;\n          &lt;img src=\&quot;../../assets/images/avatar3.jpg\&quot;&gt;\n        &lt;/mu-avatar&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Alex Qin&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;chat_bubble\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item avatar=\&quot;\&quot; button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-avatar&gt;\n          &lt;img src=\&quot;../../assets/images/avatar4.jpg\&quot;&gt;\n        &lt;/mu-avatar&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Allen Qun&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;chat_bubble\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item avatar=\&quot;\&quot; button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-avatar&gt;\n          &lt;img src=\&quot;../../assets/images/uicon.jpg\&quot;&gt;\n        &lt;/mu-avatar&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Myron Liu&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;chat_bubble\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n  &lt;/mu-list&gt;\n  &lt;mu-divider&gt;&lt;/mu-divider&gt;\n  &lt;mu-list&gt;\n    &lt;mu-sub-header&gt;History&lt;/mu-sub-header&gt;\n    &lt;mu-list-item avatar=\&quot;\&quot; button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-avatar&gt;\n          &lt;img src=\&quot;../../assets/images/avatar5.jpg\&quot;&gt;\n        &lt;/mu-avatar&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Gaia Zhou&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;chat_bubble\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item avatar=\&quot;\&quot; button :ripple=\&quot;false\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-avatar&gt;\n          &lt;img src=\&quot;../../assets/images/avatar6.jpg\&quot;&gt;\n        &lt;/mu-avatar&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;Miller Wang&lt;/mu-list-item-title&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-icon value=\&quot;chat_bubble\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-list-item-action&gt;\n    &lt;/mu-list-item&gt;\n  &lt;/mu-list&gt;\n&lt;/mu-paper&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:&quot;\n.demo-list-wrap {\n  width: 100%;\n  max-width: 360px;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-paper :z-depth="1" class="demo-list-wrap">
  <mu-appbar color="lightBlue400">
    <mu-button icon="" slot="left">
      <mu-icon value="menu"></mu-icon>
    </mu-button>
    Contacts
    <mu-button icon="" slot="right">
      <mu-icon value="search"></mu-icon>
    </mu-button>
  </mu-appbar>
  <mu-list>
    <mu-sub-header>Today</mu-sub-header>
    <mu-list-item avatar="" button="" :ripple="false">
      <mu-list-item-action>
        <mu-avatar>
          <img src="../../assets/images/avatar1.jpg">
        </mu-avatar>
      </mu-list-item-action>
      <mu-list-item-title>Mike Li</mu-list-item-title>
      <mu-list-item-action>
        <mu-icon value="chat_bubble"></mu-icon>
      </mu-list-item-action>
    </mu-list-item>
    <mu-list-item avatar="" button="" :ripple="false">
      <mu-list-item-action>
        <mu-avatar>
          <img src="../../assets/images/avatar2.jpg">
