header&gt;通知设置&lt;/mu-sub-header&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot; @click=\&quot;notifications = !notifications\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-checkbox v-model=\&quot;notifications\&quot; readonly&gt;&lt;/mu-checkbox&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;\n        通知\n      &lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot; @click=\&quot;sounds = !sounds\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-checkbox v-model=\&quot;sounds\&quot; readonly&gt;&lt;/mu-checkbox&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;\n        声音\n      &lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button :ripple=\&quot;false\&quot; @click=\&quot;videoSounds = !videoSounds\&quot;&gt;\n      &lt;mu-list-item-action&gt;\n        &lt;mu-checkbox v-model=\&quot;videoSounds\&quot; readonly&gt;&lt;/mu-checkbox&gt;\n      &lt;/mu-list-item-action&gt;\n      &lt;mu-list-item-title&gt;\n        视频的声音\n      &lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n  &lt;/mu-list&gt;\n&lt;/mu-paper&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      events: false,\n      calls: false,\n      messages: false,\n      notifications: false,\n      sounds: false,\n      videoSounds: false\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-list-wrap {\n  width: 100%;\n  max-width: 360px;\n  overflow: hidden;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-paper :z-depth="1" class="demo-list-wrap">
  <mu-appbar color="purple">
    <mu-button icon="" slot="left">
      <mu-icon value="menu"></mu-icon>
    </mu-button>
    Setting
  </mu-appbar>
  <mu-list textline="two-line">
    <mu-list-item>
      <mu-list-item-content>
        <mu-list-item-title>通知与信息设置</mu-list-item-title>
        <mu-list-item-sub-title>总是中断</mu-list-item-sub-title>
      </mu-list-item-content>
    </mu-list-item>
  </mu-list>
  <mu-divider></mu-divider>
  <mu-list>
      <mu-sub-header>声音开启设置</mu-sub-header>
      <mu-list-item button="" :ripple="false" @click="events = !events">
        <mu-list-item-title>
          事件和提醒
        </mu-list-item-title>
        <mu-list-item-action>
          <mu-switch v-model="events" readonly></mu-switch>
        </mu-list-item-action>
      </mu-list-item>
      <mu-list-item button="" :ripple="false" @click="calls = !calls">
        <mu-list-item-title>
          电话
        </mu-list-item-title>
        <mu-list-item-action>
          <mu-switch v-model="calls" readonly></mu-switch>
        </mu-list-item-action>
      </mu-list-item>
      <mu-list-item button="" :ripple="false" @click="messages = !messages">
        <mu-list-item-title>
          信息
        </mu-list-item-title>
        <mu-list-item-action>
          <mu-switch v-model="messages" readonly></mu-switch>
        </mu-list-item-action>
      </mu-list-item>
    </mu-list>
  <mu-list>
    <mu-sub-header>通知设置</mu-sub-header>
    <mu-list-item button="" :ripple="false" @click="notifications = !notifications">
      <mu-list-item-action>
        <mu-checkbox v-model="notifications" readonly></mu-checkbox>
      </mu-list-item-action>
      <mu-list-item-title>
        通知
      </mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="" :ripple="false" @click="sounds = !sounds">
      <mu-list-item-action>
        <mu-checkbox v-model="sounds" readonly></mu-checkbox>
      </mu-list-item-action>
      <mu-list-item-title>
        声音
      </mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="" :ripple="false" @click="videoSounds = !videoSounds">
      <mu-list-item-action>
        <mu-checkbox v-model="videoSounds" readonly></mu-checkbox>
      </mu-list-item-action>
      <mu-list-item-title>
        视频的声音
      </mu-list-item-title>
    </mu-list-item>
  </mu-list>
</mu-paper>


</div>
                    
