        <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"grade"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-action</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Stared<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-action</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"toggle-icon"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"24"</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"keyboard_arrow_down"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-action</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span> <span class="hljs-attr">:ripple</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"nested"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>List Item 1<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span> <span class="hljs-attr">:ripple</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"nested"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>List Item 2<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span> <span class="hljs-attr">:ripple</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"nested"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>List Item 3<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-paper</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-list-wrap</span> {
  <span class="hljs-attribute">width</span>: <span class="hljs-number">100%</span>;
  <span class="hljs-attribute">max-width</span>: <span class="hljs-number">360px</span>;
  <span class="hljs-attribute">overflow</span>: hidden;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="list-props"><a class="header-anchor" href="#list-props" aria-hidden="true">¶</a> List Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>textline</td>
<td>行数，用于改变列表项的高度</td>
<td>String</td>
<td>two-line/three-line</td>
<td>—</td>
</tr>
<tr>
<td>dense</td>
<td>是否是密集型列表，会调整列表项的高度</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>nested-indent</td>
<td>嵌套列表是否有缩进距离</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>toggle-nested</td>
<td>是否可以切换显示/隐藏嵌套列表</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>toggle-nested-type</td>
<td>切换显示类型</td>
<td>String</td>
<td>expand/popover</td>
<td>—</td>
</tr>
<tr>
<td>value</td>
<td>需要和子组件 <code v-pre="">mu-list-item</code> 配合使用时生效</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="list-events"><a class="header-anchor" href="#list-events" aria-hidden="true">¶</a> List Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当列表项被点击，<code v-pre="">value</code> 需要被改变时触发事件</td>
<td>(value)</td>
</tr>
</tbody>
</table>
<h2 id="list-item-props"><a class="header-anchor" href="#list-item-props" aria-hidden="true">¶</a> List Item Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>button</td>
<td>列表项是否可点击</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>avatar</td>
<td>是否包含 <code v-pre="">mu-avatar</code> 组件，会相应调整列表项高度</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>nested</td>
<td>是否允许嵌套列表</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>nested-list-class</td>
<td>嵌套列表的样式</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>open</td>
<td>嵌套列表是否显示</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>value</td>
<td>当 <code v-pre="">value</code> 和 <code v-pre="">mu-list</code> 组件的 <code v-pre="">value</code> 值相等时，组件呈现被选择状态</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>ripple</td>
<td>是否有波纹效果</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>href</td>
<td>相当于 a 标签的 href属性，设置之后会渲染成 a 标签，默认是使用button标签</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>to</td>
<td>表示目标路由的链接。设置之后组件会被渲染成<code v-pre="">router-link</code>, 当被点击后，内部会立刻把 <code v-pre="">to</code> 的值传到 <code v-pre="">router.push()</code>，所以这个值可以是一个字符串或者是描述目标位置的对象。</td>
<td>String/Object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>tag</td>
<td>有时候想要 <code v-pre="">&lt;router-link&gt;</code> 渲染成某种标签，例如 <code v-pre="">&lt;li&gt;</code>。 于是我们使用 <code v-pre="">tag prop</code> 类指定何种标签，同样它还是会监听点击，触发导航。</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>active-class</td>
<td>设置 链接激活时使用的 CSS 类名。默认值可以通过路由的构造选项 <code v-pre="">linkActiveClass</code> 来全局配置。</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>event</td>
<td>声明可以用来触发导航的事件。可以是一个字符串或是一个包含字符串的数组</td>
<td>String/Array</td>
<td>—</td>
<td>click</td>
</tr>
<tr>
<td>exact</td>
<td>“是否激活” 默认类名的依据是 <strong>inclusive match</strong> （全包含匹配）。 举个例子，如果当前的路径是 <code v-pre="">/a</code> 开头的，那么 <code v-pre="">&lt;router-link to=&quot;/a&quot;&gt;</code> 也会被设置 CSS 类名。</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>exact-active-class</td>
<td>配置当链接被精确匹配的时候应该激活的 class。注意默认值也是可以通过路由构造函数选项 linkExactActiveClass 进行全局配置的。</td>
<td>String</td>
<td>—</td>
<td>router-link-exact-active</td>
</tr>
<tr>
<td>append</td>
<td>设置 <code v-pre="">append</code> 属性后，则在当前（相对）路径前添加基路径。例如，我们从 <code v-pre="">/a</code> 导航到一个相对路径 b，如果没有配置 append，则路径为 /b，如果配了，则为 /a/b</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>replace</td>
<td>设置 <code v-pre="">replace</code> 属性的话，当点击时，会调用 <code v-pre="">router.replace()</code> 而不是 <code v-pre="">router.push()</code>，于是导航后不会留下 history 记录。</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
.demo-list-wrap {
  width: 100%;
  max-width: 360px;
  overflow: hidden;
}

ul.mu-list {
  padding: 8px 0;
  margin-bottom: 0;
}
</style>
<script>
export default {
  data () {
    return {
      selects: [],
      events: false,
      calls: false,
      messages: false,
      notifications: false,
      sounds: false,
      videoSounds: false,
      open: 'send'
    }
  }
}
</script>


